#experience {
    font-family: "Effra", Helvetica, sans-serif;
}

a {
    color: #5aaa9f;
}

a:hover {
    color: #ff9a9a;
    text-decoration: none;
}

.example-header {
    background: #3D4351;
    color: #FFF;
    font-weight: 300;
    padding: 3em 1em;
    text-align: center;
}

.example-header h1 {
    color: #FFF;
    font-weight: 300;
    margin-bottom: 20px;
}

.example-header p {
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 700;
}

.container-fluid .row {
    padding: 6em 0 4em 0;
}

.container-fluid .row:nth-child(even) {
